<!--  -->
<template>
  <div class=''>
    <el-card shadow="always">

      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item
          label=""
          prop="title"
        >
          <el-input v-model="ruleForm.title"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="description"
        >
          <el-input v-model="ruleForm.description"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-date-picker
            v-model="ruleForm.created"
            type="datetime"
            placeholder=""
            align="right"
            :picker-options="pickerOptions"
            value-format="yyyy-MM-dd HH:mm:ss"
            format="yyyy-MM-dd HH:mm:ss"
          >
          </el-date-picker>
        </el-form-item>

        <el-form-item label="">
          <el-switch v-model="ruleForm.top"></el-switch>
        </el-form-item>

        <el-form-item
          label=""
          prop="content"
        >
          <mavon-editor
            :codeStyle="codeStyle"
            :placeholder="'Edit '"
            ref="md"
            @imgAdd="$imgAdd"
            @imgDel="$imgDel"
            @change="change"
            :toolbars="toolbars"
            :toolbarsBackground="'#f9f9f9'"
            :ishljs="true"
            v-model="ruleForm.content"
          ></mavon-editor>
        </el-form-item>

        <el-form-item>
          <el-button
            type="primary"
            @click="submitForm('ruleForm')"
          ></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>

    </el-card>

  </div>
</template>

<script>
/**
 * /
 * 
 *       -  http://127.0.0.1:8080/blog/{blogId}
 *       - / http://127.0.0.1:8080/blog/edit
 */

export default {
  name: "blogedit",
  components: {},
  data() {
    return {
      ruleForm: {
        id: "",
        title: "",
        description: "",
        content: "",
        top: false,
        created: "",
      },
      rules: {
        title: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 108,
            message: " 3  108 ",
            trigger: "blur",
          },
        ],
        description: [
          { required: true, message: "", trigger: "blur" },
        ],
        content: [{ required: true, message: "", trigger: "blur" }],
        created: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      markdownOption: {
        // toolbars: {
        bold: false, // 
        italic: false, // 
        header: false, // 
        underline: false, // 
        strikethrough: false, // 
        mark: false, // 
        superscript: false, // 
        subscript: false, // 
        quote: false, // 
        ol: false, // 
        ul: false, // 
        link: false, // 
        imagelink: false, // 
        code: false, // code
        table: false, // 
        fullscreen: false, // 
        readmodel: false, // 
        htmlcode: false, // html
        help: false, // 
        /* 1.3.5 */
        undo: false, // 
        redo: false, // 
        trash: false, // 
        save: false, // eventssave
        /* 1.4.2 */
        navigation: false, // 
        /* 2.1.8 */
        alignleft: false, // 
        aligncenter: false, // 
        alignright: false, // 
        /* 2.2.1 */
        subfield: false, // 
        preview: false, // 
      },
      codeStyle: "monokai-sublime", //
    };
  },
  computed: {},
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios
            .post("/blog/edit", this.ruleForm, {
              headers: {
                Authorization: localStorage.getItem("token"),
              },
            })
            .then((res) => {
              this.$alert("", "", {
                confirmButtonText: "",
                callback: (action) => {
                  this.$router.push("/article/" + res.data.data.id);
                },
              });
            })
            .catch((err) => {
              console.error(err);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  created() {
    const blogId = this.$route.params.blogId;
    if (blogId) {
      this.$axios
        .get("/blog/" + blogId)
        .then((res) => {
          const blog = res.data.data;
          this.ruleForm.id = blog.id;
          this.ruleForm.description = blog.description;
          this.ruleForm.title = blog.title;
          this.ruleForm.content = blog.content;
          this.ruleForm.created = blog.created;
          this.ruleForm.top = blog.top;
        })
        .catch((err) => {
          console.error(err);
        });
    }
  },
};
</script>

<style>
</style>